.profile__edit-button {
  width: 24px;
  height: 24px;
  background: rgba(0, 0 , 0, 0);
  border: 1px solid #FFFFFF;
  background-image: url(../../../images/edit-button.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 10px;
  align-self: flex-end;

  /* –î–ª—è –≤—Å–µ—Ö –∫–ª–∏–∫–∞–±–µ–ª—å–Ω—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞—Ç—å –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤—É cursor –ø—Ä–æ—Å—Ç–æ pointer. –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞—Ç—å —É–≤–µ–ª–∏—á–µ–Ω–Ω—É—é –∏–∫–æ–Ω–∫—É –Ω–µ –Ω—É–∂–Ω–æ, —Ç–∞–∫ –∫–∞–∫ —ç—Ç–æ —Å–º–µ—â–∞–µ—Ç —Ñ–∞–∫—Ç–∏—á–µ—Å–∫—É—é —Ç–æ—á–∫—É –∫–ª–∏–∫–∞ –∫—É—Ä—Å–æ—Ä–∞.
  –°–ª–µ–¥—É–µ—Ç –∏—Å–ø—Ä–∞–≤–∏—Ç—å –¥–ª—è –≤—Å–µ—Ö —Ç–∞–∫–∏—Ö –∫—É—Ä—Å–æ—Ä–æ–≤ –≤–æ –≤—Å–µ–º –ø—Ä–æ–µ–∫—Ç–µ */
  /* cursor: url(../../../images/__cursor.svg), pointer; */

  cursor: pointer;
}

.profile__edit-button:hover {
  opacity: 0.6;
  transition: opacity .5s linear;
}

@media screen and (max-width: 425px) {
  .profile__edit-button {
    /* align-self: flex-end; */
    background-size: 7.5px;
    width: 18px;
    height: 18px;
  }
}
