.popup__container {
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  width: 430px;
  min-width: 282px;
  /* –ó–∞–¥–∞–≤–∞—Ç—å –≤—ã—Å–æ—Ç—É —ç–ª–µ–º–µ–Ω—Ç–∞–º –¥–æ–ø—É—Å—Ç–∏–º–æ –≤ —Ç–æ–º —Å–ª—É—á–∞–µ, –µ—Å–ª–∏ –Ω–µ—Ç –¥—Ä—É–≥–∏—Ö —Å–ø–æ—Å–æ–±–æ–≤ —Ä–µ—à–∏—Ç—å —ç—Ç—É –ø—Ä–æ–±–ª–µ–º—É (–Ω–∞–ø—Ä–∏–º–µ—Ä, –¥–ª—è —Ñ–æ–Ω–æ–≤–æ–π –ø–æ–¥–ª–æ–∂–∫–∏ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–π –≤—ã—Å–æ—Ç—ã). –ó–¥–µ—Å—å –≤ —ç—Ç–æ–º –Ω–µ—Ç –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏, –≤—ã—Å–æ—Ç–∞ —ç–ª–µ–º–µ–Ω—Ç–∞ –∑–∞–¥–∞–µ—Ç—Å—è –æ—Ç—Å—Ç—É–ø–∞–º–∏ –∏ –∫–æ–Ω—Ç–µ–Ω—Ç–æ–º –≤–Ω—É—Ç—Ä–∏ –Ω–µ–≥–æ */
  /* min-height: 322px; */
  position: relative;
}

@media screen and (max-width: 1280px) {
  .popup__container {
    width: calc(282px + ((100vw - 320px) / (1280 - 320)) * (430 - 282));
  }
}
